<template>
 <div>    
     <!--  -->   
    <button @click="exportExcel"></button>
    <el-button
        size="mini"
        type="info"
        @click="handleAdd()">
    </el-button>
    <div id="tableId">
        <el-table 
            :data="tableData.slice((currentPage-1)*PageSize,currentPage*PageSize)" 
            :span-method="objectSpanMethod" 
            :row-class-name="tableRowClassName"
            stripe
            border
            @cell-dblclick="dblhandleCurrentChange"  
            @cell-click="handleedicatChange"
            style="width: 100%">
            <el-table-column prop="bicRBufu" label="" width="50"></el-table-column>
            <el-table-column prop="bicRHrPositDesc" label="" width="100"></el-table-column>
            <el-table-column prop="bicRPersAreDesc" label="" width="200">
                 <!-- <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>: {{ scope.row.bicRHrPositDesc }}</p>
                        <p>: {{ scope.row.bicRPersAreDesc }}</p>
                    <div slot="reference" class="name-wrapper">
                        <el-tag size="medium">{{ scope.row.bicRPersAreDesc }}</el-tag>
                    </div>
                    </el-popover>
                </template> -->
            </el-table-column>
            <el-table-column prop="bicRZuqun" label="" width="100">
                <template slot-scope="{row}">
                    <span v-if="!isEdit[row.index]">{{row.bicRZuqun}}</span>
                    <el-input v-if="isEdit[row.index]" @blur="saveEdit(row)" v-model="row.bicRZuqun" placeholder=""></el-input>

                </template>
            </el-table-column>
            <el-table-column prop="bicRZuqunDesc" label="" width="100"></el-table-column>
            <el-table-column prop="dateTo" label="" width="100"></el-table-column>
            <el-table-column prop="ddanClassify" label="" width="100"></el-table-column>
            <el-table-column label="" v-if= "tableactive">
                <template slot-scope="scope">
                <el-button
                    size="mini"
                    @click="dialogFormVisible = true">
                </el-button>
                 <el-dialog title="" :visible.sync="dialogFormVisible">
                <el-form :model="form">
                    <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="scope.row.index" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="form.region" placeholder="">
                        <el-option label="" value="shanghai"></el-option>
                        <el-option label="" value="beijing"></el-option>
                    </el-select>
                    </el-form-item>
                </el-form>
                <!-- <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false"> </el-button>
                    <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
                </div> -->
            </el-dialog>
                <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row,tableData)">
                </el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
   
    <div class="tabListPage">
            <el-pagination @size-change="handleSizeChange" 
                        @current-change="handleCurrentChange" 
                        :current-page="currentPage" 
                        :page-sizes="pageSizes" 
                        :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper" 
                        :total="totalCount">
            </el-pagination>
    </div>
    <button @click="gotoFirst"></button>
</div>
</template>
<script>
import {
    getMessageList,
    getMenuList,
    getSelfCenterList
} from '../../services/selfPage.js'
var XLSX = require('xlsx')
var FileSaver = require('file-saver')
export default {
   data(){
       return {
            // 
            tableData:[],
            // 
            currentPage:1,
            // ()
            totalCount:1,
            // 
            pageSizes:[5,10,30,40],
            // 
            PageSize:5,
            spanArr:[],
            multipleSelection: [],
            tableactive:true,
            isEdit:[],
            objlist:{
                bicRHrPositDesc:"",
                bicRPersAreDesc:'',
                bicRZuqun:'',
                bicRZuqunDesc:""
            },
            bicRHrPositDesc:'',
            rowIndexarr:0,
            flagItem:false,
            dialogFormVisible: false,
            form: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },
            formLabelWidth: '120px'
       }
    },
    methods:{
        objectSpanMethod({ row, column, rowIndex, columnIndex }) {
            if(columnIndex ===0){
                    if(this.spanArr[rowIndex]){
                        return {
                            rowspan:this.spanArr[rowIndex],
                            colspan:1
                        }
                    }else{
                        return {
                            rowspan: 0,
                            colspan: 0
                        }
                    }
                }
        },
        exportExcel () {
            this.PageSize = this.tableData.length;//
            this.currentPage= 1;
            this.tableactive = false;
            this.$nextTick(function () {
                let wb = XLSX.utils.table_to_book(document.querySelector('#tableId'));
                /* get binary string as output */
                let wbout = XLSX.write(wb, {bookType: 'xlsx', bookSST: true, type: 'array'});
                try {
                    FileSaver.saveAs(new Blob([wbout], {type: 'application/octet-stream'}), '.xlsx')
                } catch (e) {
                    if (typeof console !== 'undefined') console.log(e, wbout)
                }
                this.PageSize = 5;//
                this.tableactive = true;                
                return wbout
            })
        },
        // downloadExl() {
        //     var wb = XLSX.utils.table_to_book(document.querySelector("#tableId"))
        //     var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
        //     try {
        //     FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'sheetjs.xlsx')
        //     } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
        // },
        // 
        // s2ab(s) {
        //     if (typeof ArrayBuffer !== 'undefind') {
        //         var buf = new ArrayBuffer(s.length);
        //         var view = new Uint8Array(buf);
        //         for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
        //         return buf;
        //     } else {
        //         var buf = new Array(s.length);
        //         for (var i = 0; i != s.length; ++i) buf[i] = s.charCodeAt(i) & 0xFF;
        //         return buf;
        //     }
        // },
        // 
        // 
        handleSizeChange(val) {
            //  
            this.PageSize=val
            // 
            this.currentPage=1
        },
            // 
        handleCurrentChange(val) {
            // 
            this.currentPage=val
        },
        // 
        handleDelete(index,rows,tableData){
            console.log(index,rows,tableData)
            tableData.splice(index, 1);//
            this.totalCount = tableData.length;
        },
        // 
        handleAdd(){
            this.tableData.push(
                {
                    bicRBufu: '', 
                    bicRHrPositDesc: '',
                    bicRPersAreDesc:'',
                    bicRZuqun:'',
                    bicRZuqunDesc:'',
                    dateTo:'',
                    ddanClassify:""
                })
             this.totalCount = this.tableData.length;
        },
        // 
        //row, column, cell, event
        dblhandleCurrentChange(row, column, cell, event){
            console.log(cell.children[0].innerText)
        },
        // 
        handleEdit(index,row){
            console.log(index)
            console.log(row)

           
            this.flagItem = true

            
        },
        tableRowClassName({row, rowIndex}) {
            row.index = rowIndex;
        },
        // input
        handleedicatChange(row, column, cell, event){
            // 
            if(column.label == '') {
                /* 
                
                */
                this.$set(this.isEdit, row.index, true)
            }
        },
        saveEdit(row) {
           
            this.$set(this.isEdit, row.index, false)
                console.log(row)
            //  
        },
        gotoFirst(){
            this.$router.push({ name:'mgtFirst'})
        }
    },
    created:function(){
        getSelfCenterList().then((data)=>{
            let arr = []
            for(var i = 0; i < 10; i++){
                arr.push(...data.data)
            }
            this.tableData = arr
            // totalCount
            this.totalCount=arr.length
            let contactDot = 0;
            this.tableData.forEach((item,index) => {
                if(index===0){
                    this.spanArr.push(1)
                }else{
                    if(item.id === this.tableData[index-1].id){
                        this.spanArr[contactDot] += 1;
                        this.spanArr.push(0)
                    }else{
                        contactDot = index
                        this.spanArr.push(1)
                    }
                }
            })
        }) 
    },
    watch:{
        objlist:{
            handler(value){
                console.log(value)
            },
            deep:true,
        }
    },
    mounted() {
        
    },
}
</script>
<style lang="scss">
    .el-table--enable-row-hover .el-table__body tr:hover>td{
        background-color: initial !important;
    }
    #tableId{
        padding:10px 20px;  
     
    }
    .tabListPage{
        padding:0 20px;
    }
</style>

