<template>
  <div class="basetable" v-loading="loading" element-loading-text="">
    <div class="selectMenu">
      <el-date-picker v-model="value6" type="daterange" placeholder="">
      </el-date-picker>
      <el-button type="primary" @click="add"></el-button>
    </div>
    <div class="tableMain">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="date" label="" width="180">
        </el-table-column>
        <el-table-column prop="name" label="" width="180">
        </el-table-column>
        <el-table-column prop="city" label="" width="180">
          <template slot-scope="scope">
            <el-select v-model="scope.row.city" placeholder="">
              <el-option v-for="item in cityList" :key="item.name" :label="item.name" :value="item.name">
                <!-- <span style="float: left">{{ item.name }}</span> -->
                <!-- <span style="float: right; color: #8492a6; font-size: 13px">{{ item.name }}</span> -->
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column prop="address" label="">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="small" @click="handleEdit(scope.$index, scope.row)">
            </el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="page">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage3" :page-size="100" layout="prev, pager, next, jumper" :total="1000">
      </el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.address" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-date-picker v-model="form.date" type="date" placeholder="">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.region" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="update"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import { reformat } from './reformartDate'

export default {
  data() {
    return {
      loading: true,
      tableData: [{
        date: '2017-05-01',
        name: '76',
        region: '',
        address: '',
        city: ''

      }, {
        date: '2017-05-02',
        name: '',
        region: '',
        address: '',
        city: ''
      }, {
        date: '2017-05-03',
        name: '',
        region: '',
        address: '',
        city: ''
      }, {
        date: '2017-05-04',
        name: '',
        region: '',
        address: '',
        city: ''
      }, {
        date: '2017-05-03',
        name: '',
        region: '',
        address: '',
        city: ''
      }, {
        date: '2017-05-03',
        name: '',
        region: '',
        address: '',
        city: ''
      }, {
        date: '2017-05-03',
        name: '',
        region: '',
        address: '',
        city: ''
      },],
      cityList: [
        { name: '' },
        { name: '' },
        { name: '' },
        { name: '' },
        { name: '' },
        { name: '' },
      ],
      dialogFormVisible: false,
      formLabelWidth: '80px',
      form: {},
      value6: '',
      currentPage3: 1,
      currentIndex: '',
      formIndex:-1,
    }
  },
  created() {
    setTimeout(() => {
      this.loading = false
    }, 1500)
  },
  methods: {
    showTime() {
      this.$alert(this.value6, '', {
        confirmButtonText: '',
        callback: action => {
          this.$message({
            type: 'info',
            message: ''
          })
        }
      })
    },
    add() {
      this.form = {
        date: '',
        name: '',
        region: '',
        address: ''
      }
      this.dialogFormVisible = true;
      console.log(this.form)
      this.tableData.push(this.form)
    },
    update() {
      this.form.date = reformat(this.form.date)
      console.log(this.form)
      // this.tableData.push(this.form)
      this.tableData.splice(this.formIndex,1,this.form)
      console.log(this.tableData)
      this.dialogFormVisible = false
    },
    handleEdit(index, row) {
      this.form = this.tableData[index]
      this.formIndex = index
      this.currentIndex = index
      this.dialogFormVisible = true
    },
    handleDelete(index, row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.tableData.splice(index, 1)
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    cancel() {
      this.dialogFormVisible = false
    },
    handleSizeChange(val) {
      console.log(` ${val} `)
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`)
    }
  },
}
</script>
<style lang="scss">
.basetable {
  .selectMenu {}
  .tableMain {
    margin: {
      top: 10px;
    }
  }
  .page {
    float: right;
    margin: {
      top: 10px;
    }
  }
}
</style>
